export default {
	data() {
		return {
			news:[]
		}
	},
	onLoad() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfo(e){
			console.log(e)
			uni.navigateTo({
				url: '../custum_work_order/custum_work_order',
			});
		}
	}
}
